.movies-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 0 auto;
  margin-top: 20px;
  max-width: 320px;
}
.movie {
  &__card {
    width: 280px;
  }
  &__poster {
    border-radius: 5px;
    max-width: 100%;
    cursor: pointer;
  }
  &__short-desc {
    margin-top: 12px;
    @include text(500, 12px, 16px, $fontColorPrimary);
  }
  &__title {
    text-transform: uppercase;
  }
  &__genre {
    color: $fontColorTertiary;
  }
}

@include tablet {
  .movies-list {
    gap: 32px;
    margin-top: 59px;
    max-width: 768px;
  }
  .movie {
    &__card {
      width: 336px;
    }
  }
}

@include desktop {
  .movies-list {
    gap: 16px;
    margin-top: 60px;
    max-width: 1280px;
  }
  .movie {
    &__card {
      width: 394px;
    }
    &__short-descr {
      @include text(500, 20px, 24px, $fontColorPrimary);
    }
  }
}
